import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';
import { Page } from '../models/page.model';
import { PagedData } from '../models/paged-data.model';
import { Medicion } from '../models/medicion.model';


@Injectable()
export class MedicionEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/medicion/todos';
  private readonly _medicionUrl: string = '/api/medicion/medicion';
  private readonly _medicionByDataloggerUrl: string = '/api/medicion/GetMedicionesByDataloggerId';
  private readonly _chartUrl: string = '/api/medicion/chart';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getResults<T>(page: Page, from: Date, to: Date): Observable<T> {
    const url = `${this._medicionUrl}/${page.pageNumber}/${page.size}/` +
      (from != null ? `${from}` : ``) + `/` + (to != null ? `${to}` : ``);

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getResults(page, from, to));
      }));
  }

  getMedicionesByDataloggerId<T>(dataloggerId: string, page: Page, from: Date, to: Date): Observable<T> {
    const endpointUrl = `${this._medicionByDataloggerUrl}/${dataloggerId}/${page.pageNumber}/` +
      (from != null ? `${from}` : ``) + `/` + (to != null ? `${to}` : ``);

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getMedicionesByDataloggerId(dataloggerId, page, from, to));
      }));
  }

  getChart<T>(dataloggerId: string, from: Date, to: Date, variable: string): Observable<T> {
    const endpointUrl = `${this._chartUrl}/${dataloggerId}/${variable}/` +
      (from != null ? `${from}` : ``) + `/` + (to != null ? `${to}` : ``);

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getChart(dataloggerId, from, to, variable));
      }));
  }

  getMedicionByIdEndpoint<T>(medID: number): Observable<T> {
    const endpointUrl = `${this._medicionUrl}/${medID}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getMedicionByIdEndpoint(medID));
      }));
  }

  getNewMedicionEndpoint<T>(medObject: any): Observable<T> {

    return this.http.post<T>(this._medicionUrl, JSON.stringify(medObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewMedicionEndpoint(medObject));
      }));
  }

  getUpdateMedicionEndpoint<T>(medObject: any, medId: string): Observable<T> {
    const endpointUrl = `${this._medicionUrl}/${medId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(medObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateMedicionEndpoint(medObject, medId));
      }));
  }

  getDeleteMedicionEndpoint<T>(medId: number): Observable<T> {
    const endpointUrl = `${this._medicionUrl}/${medId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteMedicionEndpoint(medId));
      }));
  }
}
