import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { DataLoggerEndpoint } from './data-logger-endpoint.service';
import { AuthService } from './auth.service';
import { DataLogger } from '../models/data-logger.model';


@Injectable()
export class DataLoggerService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private dataLoggerEndpoint: DataLoggerEndpoint) {

  }

  getDataLoggerByIdEndpoint(dlId: string) {
    return this.dataLoggerEndpoint.getDataLoggerByIdEndPoint<DataLogger>(dlId);
  }

  getAll(page?: number, pageSize?: number) {
    return this.dataLoggerEndpoint.getAll<DataLogger[]>(page, pageSize);
  }

  getDataLoggersAvailable(configID: number) {
    return this.dataLoggerEndpoint.getDataLoggersAvailableEndpoint<DataLogger[]>(configID);
  }

  updateDataLogger(dl: DataLogger) {
      return this.dataLoggerEndpoint.getUpdateDataLoggerEndpoint(dl, dl.id.toString());
  }

  newDataLogger(dl: DataLogger) {
    return this.dataLoggerEndpoint.getNewDataLoggerEndpoint<DataLogger>(dl);
  }


  deleteDataLogger(dlOrEstId: string | DataLogger): Observable<DataLogger> {
    if (typeof dlOrEstId === 'string' || dlOrEstId instanceof String) {
      return this.dataLoggerEndpoint.getDeleteDataLoggerEndpoint<DataLogger>(<string>dlOrEstId);
    }
    else {

      if (dlOrEstId.id) {
        return this.deleteDataLogger(dlOrEstId.id.toString());
      }
    }
  }
}
