export class Alarma {
  public id: number;
  public fechayHoraAlarma: Date;
  public codigoAlarma: number;
  public valorRegistrado: number;
  public alarmaDisparada: boolean;
  public dataloggerID: string;
  public datalogger: string;

  constructor(config?: Alarma) {
    if (config != null) {
      this.id = config.id;
      this.dataloggerID = config.dataloggerID;
      this.datalogger = config.datalogger;
      this.fechayHoraAlarma = config.fechayHoraAlarma;
      this.codigoAlarma = config.codigoAlarma;
      this.valorRegistrado = config.valorRegistrado;
      this.alarmaDisparada = config.alarmaDisparada;   
    }
  }

  public Copy(config: Alarma) {
    config.id = this.id;
    config.dataloggerID = this.dataloggerID;
    config.datalogger = this.datalogger;
    config.fechayHoraAlarma = this.fechayHoraAlarma;
    config.codigoAlarma = this.codigoAlarma;
    config.valorRegistrado = this.valorRegistrado;
    config.alarmaDisparada = this.alarmaDisparada;
  }
}
