export class SistemaFotovoltaico {
  public id: number;
  public establecimientoID: number;
  public establecimiento: string;
  public potenciaPicoModulo: number;
  public tensionPicoModulo: number;
  public potenciaPicoGenerador: number;
  public cantidadReguladoresCarga: number;
  public potenciaPicoGenerador1: number;
  public potenciaPicoGenerador2: number;
  public potenciaPicoGenerador3: number;
  public cantidadModulosGenerador1: number;
  public cantidadModulosGenerador2: number;
  public cantidadModulosGenerador3: number;
  public tensionNominalBancoBaterias: number;
  public capacidadNominalBancoBaterias: number;
  public corrienteNominalCargadorBaterias: number;

  constructor(config?: SistemaFotovoltaico) {
    if (config != null) {
      this.id = config.id;
      this.establecimientoID = config.establecimientoID;
      this.establecimiento = config.establecimiento;
      this.potenciaPicoModulo = config.potenciaPicoModulo;
      this.tensionPicoModulo = config.tensionPicoModulo;
      this.potenciaPicoGenerador = config.potenciaPicoGenerador;
      this.cantidadReguladoresCarga = config.cantidadReguladoresCarga;
      this.potenciaPicoGenerador1 = config.potenciaPicoGenerador1;
      this.potenciaPicoGenerador2 = config.potenciaPicoGenerador2;
      this.potenciaPicoGenerador3 = config.potenciaPicoGenerador3;
      this.cantidadModulosGenerador1 = config.cantidadModulosGenerador1;
      this.cantidadModulosGenerador2 = config.cantidadModulosGenerador2;
      this.cantidadModulosGenerador3 = config.cantidadModulosGenerador3;
      this.tensionNominalBancoBaterias = config.tensionNominalBancoBaterias;
      this.capacidadNominalBancoBaterias = config.capacidadNominalBancoBaterias;
      this.corrienteNominalCargadorBaterias = config.corrienteNominalCargadorBaterias;
    }
  }

  public Copy(config: SistemaFotovoltaico) {
    config.id = this.id;
    config.establecimientoID = this.establecimientoID;
    config.establecimiento = this.establecimiento;
    config.potenciaPicoModulo = this.potenciaPicoModulo;
    config.tensionPicoModulo = this.tensionPicoModulo;
    config.potenciaPicoGenerador = this.potenciaPicoGenerador;
    config.cantidadReguladoresCarga = this.cantidadReguladoresCarga;
    config.potenciaPicoGenerador1 = this.potenciaPicoGenerador1;
    config.potenciaPicoGenerador2 = this.potenciaPicoGenerador2;
    config.potenciaPicoGenerador3 = this.potenciaPicoGenerador3;
    config.cantidadModulosGenerador1 = this.cantidadModulosGenerador1;
    config.cantidadModulosGenerador2 = this.cantidadModulosGenerador2;
    config.cantidadModulosGenerador3 = this.cantidadModulosGenerador3;
    config.tensionNominalBancoBaterias = this.tensionNominalBancoBaterias;
    config.capacidadNominalBancoBaterias = this.capacidadNominalBancoBaterias;
    config.corrienteNominalCargadorBaterias = this.corrienteNominalCargadorBaterias;
  }
}
