import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { EstablecimientoEndpoint } from './establecimiento-endpoint.service';
import { AuthService } from './auth.service';
import { Establecimiento } from '../models/establecimiento.model';


@Injectable()
export class EstablecimientoService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private establecimientoEndpoint: EstablecimientoEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.establecimientoEndpoint.getAll<Establecimiento[]>(page, pageSize);
  }

  updateEstablecimiento(est: Establecimiento) {
    if (est.id) {
      return this.establecimientoEndpoint.getUpdateEstablecimientoEndpoint(est, est.id.toString());
    }
    else {
      return this.establecimientoEndpoint.getEstablecimientoByNombreEndpoint<Establecimiento>(est.nombre).pipe<Establecimiento>(
        mergeMap(foundEst => {
          est.id = foundEst.id;
          return this.establecimientoEndpoint.getUpdateEstablecimientoEndpoint(est, est.id.toString());
        }));
    }
  }

  newEstablecimiento(est: Establecimiento) {
    return this.establecimientoEndpoint.getNewEstablecimientoEndpoint<Establecimiento>(est);
  }


  deleteEstablecimiento(estOrEstId: string | Establecimiento): Observable<Establecimiento> {
    if (typeof estOrEstId === 'string' || estOrEstId instanceof String) {
      return this.establecimientoEndpoint.getDeleteEstablecimientoEndpoint<Establecimiento>(<string>estOrEstId);
    }
    else {

      if (estOrEstId.id) {
        return this.deleteEstablecimiento(estOrEstId.id.toString());
      }
      else {
        return this.establecimientoEndpoint.getEstablecimientoByNombreEndpoint<Establecimiento>(estOrEstId.nombre).pipe<Establecimiento>(
          mergeMap(est => this.deleteEstablecimiento(est.id.toString())));
      }
    }
  }
}
