import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class ConfiguracionMedicionesEndpoint extends EndpointFactory {
  
  private readonly _configuracionMedicionesUrl: string = '/api/configuracionMediciones/configuracionMediciones';
  private readonly _todosUrl: string = '/api/configuracionMediciones/todos';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getConfiguracionMedicionesByIDEndpoint<T>(id: number): Observable<T> {
    const endpointUrl = `${this._configuracionMedicionesUrl}/${id}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getConfiguracionMedicionesByIDEndpoint(id));
      }));
  }

  getNewConfiguracionMedicionesEndpoint<T>(estObject: any): Observable<T> {

    return this.http.post<T>(this._configuracionMedicionesUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewConfiguracionMedicionesEndpoint(estObject));
      }));
  }

  getUpdateConfiguracionMedicionesEndpoint<T>(estObject: any, estId: string): Observable<T> {
    const endpointUrl = `${this._configuracionMedicionesUrl}/${estId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateConfiguracionMedicionesEndpoint(estObject, estId));
      }));
  }

  getDeleteConfiguracionMedicionesEndpoint<T>(estId: number): Observable<T> {
    const endpointUrl = `${this._configuracionMedicionesUrl}/${estId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteConfiguracionMedicionesEndpoint(estId));
      }));
  }
}
