import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class DataLoggerEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/dataLogger/todos';
  private readonly _dataLoggerUrl: string = '/api/dataLogger/dataLogger';
  private readonly _dataLoggerAvailable: string = '/api/dataLogger/available';
  private readonly _dataLoggerByNombreUrl: string = '/api/dataLogger/dataLogger/nombre';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getDataLoggerByIdEndPoint<T>(dlId: string): Observable<T> {
    const endpointUrl = `${this._dataLoggerUrl}/${dlId}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDataLoggerByIdEndPoint(dlId));
      }));
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getDataLoggersAvailableEndpoint<T>(configID: number): Observable<T> {
    const url = `${this._dataLoggerAvailable}/${configID}`;
 
    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDataLoggersAvailableEndpoint(configID));
      }));
  }

  getNewDataLoggerEndpoint<T>(estObject: any): Observable<T> {

    return this.http.post<T>(this._dataLoggerUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewDataLoggerEndpoint(estObject));
      }));
  }

  getUpdateDataLoggerEndpoint<T>(estObject: any, estId: string): Observable<T> {
    const endpointUrl = `${this._dataLoggerUrl}/${estId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateDataLoggerEndpoint(estObject, estId));
      }));
  }

  getDeleteDataLoggerEndpoint<T>(estId: string): Observable<T> {
    const endpointUrl = `${this._dataLoggerUrl}/${estId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteDataLoggerEndpoint(estId));
      }));
  }
}
