import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class ConfiguracionEventoEndpoint extends EndpointFactory {
  
  private readonly _configuracionEventoUrl: string = '/api/configuracionEvento/cevento';
  private readonly _todosUrl: string = '/api/configuracionEvento/todos';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getConfiguracionEventoByIDEndpoint<T>(id: number): Observable<T> {
    const endpointUrl = `${this._configuracionEventoUrl}/${id}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getConfiguracionEventoByIDEndpoint(id));
      }));
  }

  getNewConfiguracionEventoEndpoint<T>(ceveObject: any): Observable<T> {

    return this.http.post<T>(this._configuracionEventoUrl, JSON.stringify(ceveObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewConfiguracionEventoEndpoint(ceveObject));
      }));
  }

  getUpdateConfiguracionEventoEndpoint<T>(ceveObject: any, ceveId: string): Observable<T> {
    const endpointUrl = `${this._configuracionEventoUrl}/${ceveId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(ceveObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateConfiguracionEventoEndpoint(ceveObject, ceveId));
      }));
  }

  getDeleteConfiguracionEventoEndpoint<T>(ceveId: number): Observable<T> {
    const endpointUrl = `${this._configuracionEventoUrl}/${ceveId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteConfiguracionEventoEndpoint(ceveId));
      }));
  }
}
