
import { Component, OnInit, OnDestroy, Input, EventEmitter, ViewChild, Output, TemplateRef } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { ConfiguracionDeRedService } from '../../services/configuracion-de-red.service';
import { ConfiguracionDeRed } from 'src/app/models/configuracion-de-red.model';
import { DataLoggerService } from '../../services/data-logger.service';
import { DataLogger } from 'src/app/models/data-logger.model';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-abm-configuracion-de-red',
  templateUrl: './abm-configuracion-de-red.component.html',
  styleUrls: ['./abm-configuracion-de-red.component.css']
})
export class AbmConfiguracionDeRedComponent implements OnInit, OnDestroy {
  rows: ConfiguracionDeRed[] = [];
  rowsCache: ConfiguracionDeRed[] = [];
  columns = [];
  editing = {};
  itemView: ConfiguracionDeRed = new ConfiguracionDeRed();
  itemEdit: ConfiguracionDeRed = new ConfiguracionDeRed();
  isDataLoaded = false
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  _isCreate = false;
  _isClone = false;
  _rowEdit: ConfiguracionDeRed = new ConfiguracionDeRed();

  dataloggers: DataLogger[] = [];

  @Input() _showDataloggerEdit: number;

  //@Input() _dataLoggerID: string;

  //@Output() UpdatedItem = new EventEmitter();


  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModalRed')
  viewModalRed: ModalDirective;

  allData: ConfiguracionDeRed[];

  constructor(private alertService: AlertService, private translationService: AppTranslationService,
    private localStorage: LocalStoreManager, private authService: AuthService, private configuracionDeRedService: ConfiguracionDeRedService, private dataLoggerService : DataLoggerService) {
  }

  private gT = (key: string) => this.translationService.getTranslation(key);


  ngOnInit() {
    this.loadingIndicator = true;
    
    this.dataLoggerService.getDataLoggersAvailable(1).subscribe(p => {
      this.dataloggers = p;
      //setTimeout(() => { this.loadingIndicator = false; }, 1000);
    });
    

    this.fetch((data) => {      
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });

    this.columns = [
      {
        prop: 'dataloggerID',
        name: this.gT('configuracionDeRedes.DataloggerID'),
        width: 175
      },
      {
        prop: 'dhcpEthernet',
        name: this.gT('configuracionDeRedes.DhcpEthernet'),
        width: 175
      },
      {
        prop: 'direccionIpEthernet',
        name: this.gT('configuracionDeRedes.DireccionIpEthernet'),
        width: 175
      },
      {
        prop: 'mascaraSubredEthernet',
        name: this.gT('configuracionDeRedes.MascaraSubredEthernet'),
        width: 175
      },
      {
        prop: 'puertaEnlaceEthernet',
        name: this.gT('configuracionDeRedes.PuertaEnlaceEthernet'),
        width: 175
      },
      {
        prop: 'dnsPrimarioEthernet',
        name: this.gT('configuracionDeRedes.DnsPrimarioEthernet'),
        width: 175
      },
      {
        prop: 'dnsSecundarioEthernet',
        name: this.gT('configuracionDeRedes.DnsSecundarioEthernet'),
        width: 175
      },
      {
        prop: 'ssidWifi',
        name: this.gT('configuracionDeRedes.SsidWifi'),
        width: 175
      },
      {
        prop: 'seguridadWifi',
        name: this.gT('configuracionDeRedes.SeguridadWifi'),
        width: 175
      },
      {
        prop: 'dchpWifi',
        name: this.gT('configuracionDeRedes.DchpWifi'),
        width: 175
      },
      {
        prop: 'direccionIpWifi',
        name: this.gT('configuracionDeRedes.DireccionIpWifi'),
        width: 175
      },
      {
        prop: 'mascaraSubredWifi',
        name: this.gT('configuracionDeRedes.MascaraSubredWifi'),
        width: 175
      },
      {
        prop: 'puertaEnlaceWifi',
        name: this.gT('configuracionDeRedes.PuertaEnlaceWifi'),
        width: 175
      },
      {
        prop: 'dnsPrimarioWifi',
        name: this.gT('configuracionDeRedes.DnsPrimarioWifi'),
        width: 175
      },
      {
        prop: 'dnsSecundarioWifi',
        name: this.gT('configuracionDeRedes.DnsSecundarioWifi'),
        width: 175
      },
      {
        prop: 'urlServidorCentral',
        name: this.gT('configuracionDeRedes.UrlServidorCentral'),
        width: 175
      },
      {
        prop: 'puertoServidorCentral',
        name: this.gT('configuracionDeRedes.PuertoServidorCentral'),
        width: 175
      },
      {
        prop: 'timeOutConexionServidor',
        name: this.gT('configuracionDeRedes.TimeOutConexionServidor'),
        width: 175
      },
      {
        prop: 'timeOutIntercambioDatosServidor',
        name: this.gT('configuracionDeRedes.TimeOutIntercambioDatosServidor'),
        width: 175
      },
      {
        prop: 'reintentosReconexionPorCiclo',
        name: this.gT('configuracionDeRedes.ReintentosReconexionPorCiclo'),
        width: 175
      },
      {
        prop: 'tiempoEsperaEntreReconexiones',
        name: this.gT('configuracionDeRedes.TiempoEsperaEntreReconexiones'),
        width: 175
      },
      {
        prop: 'tiempoEsperaRecomienzoDeCiclo',
        name: this.gT('configuracionDeRedes.TiempoEsperaRecomienzoDeCiclo'),
        width: 175
      },  
      {
        name: '', width: 110, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false,
      }
    ];

  }

  ngOnDestroy() {
  }

  fetch(cb) {
    setTimeout(() => {
      this.configuracionDeRedService.getAll().subscribe(e => {

        for (let dl of e) {
          for (let p of this.dataloggers)
            if (p.id == dl.dataloggerID) {
              dl.datalogger = p.id;
              break;
            }
        }

        cb(e);
      })
    }, 1000);
  }

  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }

  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.datalogger));
  }

  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    setTimeout(() => {
      this.itemView = row;
      this.viewModalRed.show();
    }, 1000);
  }

  edit(row) {
    this._isCreate = false;
    this._isClone = false;
    this._showDataloggerEdit = 0;
    this._rowEdit = row;
    this.itemEdit = new ConfiguracionDeRed(row);
    this.editorModal.show();
  }

  clone(row) {
    this._isClone = true;
    this._isCreate = false;
    this._showDataloggerEdit = 1;
    this._rowEdit = row;
    this.itemEdit = new ConfiguracionDeRed(row);
    this.editorModal.show();
  }

  addElement() {
    setTimeout(() => {
      this._isCreate = true;
      this._isClone = false;
      this._showDataloggerEdit = 1;
      this.itemEdit = new ConfiguracionDeRed();
      this.editorModal.show();
    }, 1000);
  }


  //addElement() {
  //  this.itemEdit = new ConfiguracionDeRed();
  //  this.editorModal.show();
  //}

  //editElement() {
  //  this.configuracionDeRedService.getConfiguracionByIdEndpoint(this._itemID).subscribe(est => {
  //    this.itemEdit = est;
  //    this.editorModal.show();
  //  }, error => this.showFailedMessage(error));
  //}

  //viewElement() {
  //  this.configuracionDeRedService.getConfiguracionByIdEndpoint(this._itemID).subscribe(est => {
  //    this.itemEdit = this.itemView = est;
  //    this.viewModalRed.show();
  //  }, error => this.showFailedMessage(error));
  //}

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate)
      this.configuracionDeRedService.newConfiguracionDeRed(this.itemEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
    else if (!this._isCreate && !this._isClone)
      this.configuracionDeRedService.updateConfiguracionDeRed(this.itemEdit).subscribe(est => this.updateSuccessHelper(), error => this.showFailedMessage(error));

    if (this._isClone)
      this.configuracionDeRedService.newConfiguracionDeRed(this.itemEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
  }

  //createSuccessHelper(id) {
  //  if (!this.itemEdit.id || this.itemEdit.id == 0) {
  //    this.itemEdit.id = id;
  //    this.UpdatedItem.emit({ id: this.itemEdit.id });
  //  }
  //  this.itemView = this.itemEdit;
  //  this.editorModal.hide();
  //  this.showSuccessMessage();
  //}

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.itemEdit);
    this.rows.splice(0, 0, this.itemEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.itemEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage(error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }


  deleteHelper(row) {
    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.configuracionDeRedService.deleteConfiguracionDeRed(row.id)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar'), MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }  
}

