
import { Component, OnInit, OnDestroy, Input, EventEmitter, ViewChild, Output, TemplateRef } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { ConfiguracionDeAlarmasService } from '../../services/configuracion-de-alarmas.service';
import { ConfiguracionDeAlarmas } from 'src/app/models/configuracion-de-alarmas.model';
import { DataLoggerService } from '../../services/data-logger.service';
import { DataLogger } from 'src/app/models/data-logger.model';


@Component({
  selector: 'app-abm-configuracion-de-alarmas',
  templateUrl: './abm-configuracion-de-alarmas.component.html',
  styleUrls: ['./abm-configuracion-de-alarmas.component.css']
})
export class AbmConfiguracionDeAlarmasComponent implements OnInit, OnDestroy {
  rows: ConfiguracionDeAlarmas[] = [];
  rowsCache: ConfiguracionDeAlarmas[] = [];
  columns = [];
  editing = {};
  itemView: ConfiguracionDeAlarmas = new ConfiguracionDeAlarmas();
  itemEdit: ConfiguracionDeAlarmas = new ConfiguracionDeAlarmas();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  random: number = Math.floor((Math.random() * 6) + 1);
  _isCreate = false;
  _isClone = false;
  _rowEdit : ConfiguracionDeAlarmas = new ConfiguracionDeAlarmas();

  dataloggers: DataLogger[] = [];

  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;

  @Input() _showDataloggerEdit: number;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModalAlarmas')
  viewModalAlarmas: ModalDirective;

  allData: ConfiguracionDeAlarmas[];

  constructor(private alertService: AlertService, private translationService: AppTranslationService,
    private localStorage: LocalStoreManager, private authService: AuthService, private configuracionDeAlarmasService: ConfiguracionDeAlarmasService, private _Activatedroute: ActivatedRoute, private dataLoggerService: DataLoggerService) {
  }

  private gT = (key: string) => this.translationService.getTranslation(key);

  ngOnInit() {
    this.loadingIndicator = true;

    this.dataLoggerService.getDataLoggersAvailable(2).subscribe(p => {
      this.dataloggers = p;
    });

    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });

    this.columns = [
      {
        prop: 'dataloggerID',
        name: this.gT('configuracionDeAlarmas.DataloggerID'),
        width: 175
      },
      {
        prop: 'tensionAlarmaBajaBateria',
        name: this.gT('configuracionDeAlarmas.TensionAlarmaBajaBateria'),
        width: 125
      },
      {
        prop: 'tiempoAlarmaBajaBateria',
        name: this.gT('configuracionDeAlarmas.TiempoAlarmaBajaBateria'),
        width: 125
      },
      {
        prop: 'tensionRAlBajaBateria',
        name: this.gT('configuracionDeAlarmas.TensionRAlBajaBateria'),
        width: 125
      },
      {
        prop: 'tiempoRAlBajaBateria',
        name: this.gT('configuracionDeAlarmas.TiempoRAlBajaBateria'),
        width: 175
      },
      {
        prop: 'alBajaBateriaHabilitada',
        name: this.gT('configuracionDeAlarmas.AlBajaBateriaHabilitada'),
        width: 175
      },
      {
        prop: 'sobretensionBateria',
        name: this.gT('configuracionDeAlarmas.SobretensionBateria'),
        width: 175
      },
      {
        prop: 'tensionAlSobretensionDeBateria',
        name: this.gT('configuracionDeAlarmas.TensionAlSobretensionDeBateria'),
        width: 175
      },
      {
        prop: 'tiempoAlSobretensionDeBateria',
        name: this.gT('configuracionDeAlarmas.TiempoAlSobretensionDeBateria'),
        width: 175
      },
      {
        prop: 'tensionRAlSobretensionDeBateria',
        name: this.gT('configuracionDeAlarmas.TensionRAlSobretensionDeBateria'),
        width: 175
      },
      {
        prop: 'tiempoRAlSobretensionDeBateria',
        name: this.gT('configuracionDeAlarmas.TiempoRAlSobretensionDeBateria'),
        width: 175
      },
      {
        prop: 'alSobretensionDeBateriaHabilitada',
        name: this.gT('configuracionDeAlarmas.AlSobretensionDeBateriaHabilitada'),
        width: 175
      },
      {
        name: '', width: 100, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false,
      }
    ];
  }

  ngOnDestroy() {
  }

  fetch(cb) {
      this.configuracionDeAlarmasService.getAll().subscribe(e => {

        for (let dl of e) {
          for (let p of this.dataloggers)
            if (p.id == dl.dataloggerID) {
              dl.datalogger = p.id;
              break;
            }
        }

        cb(e);
      })
  }

    refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }

  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    this.CloseCollapsables();
    this.itemView = row;
    this.viewModalAlarmas.show();
  }

  edit(row) {
    this.CloseCollapsables();
    this._isCreate = false;
    this._isClone = false;
    this._showDataloggerEdit = 0;
    this._rowEdit = row;
    this.itemEdit = new ConfiguracionDeAlarmas(row);
    this.editorModal.show();
  }

  loadDataloggers(d) {
    this.dataloggers = d;
  }

  clone(row) {
    this._isClone = true;
    this._isCreate = false;
    this._showDataloggerEdit = 1;
    this._rowEdit = row;
    this.itemEdit = new ConfiguracionDeAlarmas(row);
    this.editorModal.show();
  }

  addElement() {
    this.CloseCollapsables();
    this._isCreate = true;
    this._isClone = false;
    this._showDataloggerEdit = 1;
    this.itemEdit = new ConfiguracionDeAlarmas();
    this.editorModal.show();
  }

  CloseCollapsables() {
    var array = document.getElementsByTagName("input");
    for (var ii = 0; ii < array.length; ii++) {

      if (array[ii].type == "checkbox") {
        if (array[ii].className == "toggle collapsable-check") {
          array[ii].checked = false;
        }
      }
    }
  }
  
  
  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate)
      this.configuracionDeAlarmasService.newConfiguracionDeAlarmas(this.itemEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
    else if (!this._isCreate && !this._isClone)
      this.configuracionDeAlarmasService.updateConfiguracionDeAlarmas(this.itemEdit).subscribe(est => this.updateSuccessHelper(), error => this.showFailedMessage(error));

    if (this._isClone)
      this.configuracionDeAlarmasService.newConfiguracionDeAlarmas(this.itemEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.itemEdit);
    this.rows.splice(0, 0, this.itemEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.itemEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage(error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }
  
  deleteHelper(row) {
    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.configuracionDeAlarmasService.deleteConfiguracionDeAlarmas(row.id)
      .subscribe(results => this.deleteHelperSuccess(row), error => this.deleteHelperError(error));
  }
  
  deleteHelperSuccess(row) {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar'), MessageSeverity.success);
  }

  deleteHelperError(error) {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
  }

  collapseNext(id) {
    var cb = <HTMLInputElement>document.getElementById(this.random+id);
    cb.checked = !cb.checked;
  }
}

