export class Evento {
  public id: number;
  public fecha: Date;
  public codigo: number;
  public dataloggerID: string;
  public datalogger: string;

  constructor(config?: Evento) {
    if (config != null) {
      this.id = config.id;
      this.dataloggerID = config.dataloggerID;
      this.datalogger = config.datalogger;
      this.fecha = config.fecha;
      this.codigo = config.codigo;
    }
  }

  public Copy(config: Evento) {
    config.id = this.id;
    config.dataloggerID = this.dataloggerID;
    config.datalogger = this.datalogger;
    config.fecha = this.fecha;
    config.codigo = this.codigo;
  }

}
