import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin, of } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { MedicionEndpoint } from './medicion-endpoint.service';
import { AuthService } from './auth.service';
import { Medicion } from '../models/medicion.model';
import { Page } from '../models/page.model';
import { PagedData } from '../models/paged-data.model';
import { Chart } from '../models/chart.model';


@Injectable()
export class MedicionService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private medicionEndpoint: MedicionEndpoint) {

  }

  getResults(page: Page, from: Date, to: Date): Observable<PagedData<Medicion>> {
    return this.medicionEndpoint.getResults<PagedData<Medicion>>(page, from, to);
  }

  getMedicionesByDataloggerId(dataloggerId: string, page: Page, from: Date, to: Date): Observable<PagedData<Medicion>> {
    return this.medicionEndpoint.getMedicionesByDataloggerId<PagedData<Medicion>>(dataloggerId, page, from, to);
  }

  

  getChart(dataloggerId: string, from: Date, to: Date, variable: string): Observable<Chart> {
    //var finalVar;

    //if (typeof variable === "string") {
    //  finalVar = variable.slice();
    //}
    //else {
    //  finalVar = (variable.length > 0 ? variable.join(',') : "");
    //}

    return this.medicionEndpoint.getChart<Chart>(dataloggerId, from, to, variable);
  }

  getMedicionesById(medId: number) {
    return this.medicionEndpoint.getMedicionByIdEndpoint<Medicion>(medId);
  }

  updateMedicion(med: Medicion) {
    if (med.id) {
      return this.medicionEndpoint.getUpdateMedicionEndpoint(med, med.id.toString());
    }
    else {
      return this.medicionEndpoint.getMedicionByIdEndpoint<Medicion>(med.id).pipe<Medicion>(
        mergeMap(foundMed => {
          med.id = foundMed.id;
          return this.medicionEndpoint.getUpdateMedicionEndpoint(med, med.id.toString());
        }));
    }
  }

  newMedicion(med: Medicion) {
    return this.medicionEndpoint.getNewMedicionEndpoint<Medicion>(med);
  }


  deleteMedicion(medId: number): Observable<Medicion> {
    return this.medicionEndpoint.getDeleteMedicionEndpoint<Medicion>(medId);
  }
}
