import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class ConfiguracionDeAlarmasEndpoint extends EndpointFactory {

  private readonly _configuracionUrl: string = '/api/configuracionDeAlarmas/configuracion';
  private readonly _configuracionDeAlarmasUrl: string = '/api/configuracionDeAlarmas/configuracionDeAlarmas';
  private readonly _todosUrl: string = '/api/configuracionDeAlarmas/todos';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getConfiguracionByIdEndPoint<T>(configId: number): Observable<T> {
    const endpointUrl = `${this._configuracionUrl}/${configId}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getConfiguracionByIdEndPoint(configId));
      }));
  }

  getNewConfiguracionDeAlarmasEndPoint<T>(configObject: any): Observable<T> {

    return this.http.post<T>(this._configuracionUrl, JSON.stringify(configObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewConfiguracionDeAlarmasEndPoint(configObject));
      }));
  }

  getUpdateConfiguracionDeAlarmasEndPoint<T>(configObject: any, configId: number): Observable<T> {
    const endpointUrl = `${this._configuracionUrl}/${configId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(configObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateConfiguracionDeAlarmasEndPoint(configObject, configId));
      }));
  }

  getDeleteConfiguracionDeAlarmasEndPoint(configId: number) {
    const endpointUrl = `${this._configuracionDeAlarmasUrl}/${configId}`;

    return this.http.delete(endpointUrl, this.getRequestHeaders()).pipe(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteConfiguracionDeAlarmasEndPoint(configId));
      }));
  }
}
