import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class SistemaFotovoltaicoEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/sistemaFotovoltaico/todos';
  private readonly _sistemaFotovoltaicoUrl: string = '/api/sistemaFotovoltaico/sistemaFotovoltaico';
  private readonly _sistemaFotovoltaicoBySistemaFotovoltaicoUrl: string = '/api/sistemaFotovoltaico/sistemaFotovoltaico/establecimiento';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getSistemaFotovoltaicoByEstablecimientoEndpoint<T>(estID: number): Observable<T> {
    const endpointUrl = `${this._sistemaFotovoltaicoBySistemaFotovoltaicoUrl}/${estID}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getSistemaFotovoltaicoByEstablecimientoEndpoint(estID));
      }));
  }

  getNewSistemaFotovoltaicoEndpoint<T>(estObject: any): Observable<T> {

    return this.http.post<T>(this._sistemaFotovoltaicoUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewSistemaFotovoltaicoEndpoint(estObject));
      }));
  }

  getUpdateSistemaFotovoltaicoEndpoint<T>(estObject: any, estId: string): Observable<T> {
    const endpointUrl = `${this._sistemaFotovoltaicoUrl}/${estId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateSistemaFotovoltaicoEndpoint(estObject, estId));
      }));
  }

  getDeleteSistemaFotovoltaicoEndpoint<T>(estId: number): Observable<T> {
    const endpointUrl = `${this._sistemaFotovoltaicoUrl}/${estId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteSistemaFotovoltaicoEndpoint(estId));
      }));
  }
}
