
import { Component, OnInit, OnDestroy, Input, TemplateRef, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { SistemaFotovoltaicoService } from '../../services/sistema-fotovoltaico.service';
import { SistemaFotovoltaico } from 'src/app/models/sistema-fotovoltaico.model';
import { EstablecimientoService } from '../../services/establecimiento.service';
import { Establecimiento } from 'src/app/models/establecimiento.model';
import { Permission } from '../../models/permission.model';
import { AccountService } from '../../services/account.service';

@Component({
  selector: 'app-abm-sistema-fotovoltaico',
  templateUrl: './abm-sistema-fotovoltaico.component.html',
  styleUrls: ['./abm-sistema-fotovoltaico.component.css']
})
export class AbmSistemaFotovoltaicoComponent implements OnInit, OnDestroy {
  public static readonly DBKeySistemasFotovoltaicos = 'abm-sistema-fotovoltaico.sistemas-fotovoltaicos';

  rows: SistemaFotovoltaico[] = [];
  rowsCache: SistemaFotovoltaico [] = [];
  columns = [];
  editing = {};
  sistemaFotovoltaicoView: SistemaFotovoltaico = new SistemaFotovoltaico();
  sistemaFotovoltaicoEdit: SistemaFotovoltaico = new SistemaFotovoltaico();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  _isCreate = false;
  _rowEdit: SistemaFotovoltaico = new SistemaFotovoltaico();

  establecimientos: Establecimiento[] = [];

  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;
  @ViewChild('headerEstablecimientoTemplate')
  headerEstablecimientoTemplate: TemplateRef<any>;

  @ViewChild('headerPotenciaPicoModuloTemplate')
  headerPotenciaPicoModuloTemplate: TemplateRef<any>;

  @ViewChild('headerTensionPicoModuloTemplate')
  headerTensionPicoModuloTemplate: TemplateRef<any>;

  @ViewChild('headerPotenciaPicoGeneradorTemplate')
  headerPotenciaPicoGeneradorTemplate: TemplateRef<any>;

  @ViewChild('headerCantidadReguladoresCargaTemplate')
  headerCantidadReguladoresCargaTemplate: TemplateRef<any>;

  @ViewChild('headerPotenciaPicoGenerador1Template')
  headerPotenciaPicoGenerador1Template: TemplateRef<any>;

  @ViewChild('headerPotenciaPicoGenerador2Template')
  headerPotenciaPicoGenerador2Template: TemplateRef<any>;

  @ViewChild('headerPotenciaPicoGenerador3Template')
  headerPotenciaPicoGenerador3Template: TemplateRef<any>;

  @ViewChild('headerCantidadModulosGenerador1Template')
  headerCantidadModulosGenerador1Template: TemplateRef<any>;

  @ViewChild('headerCantidadModulosGenerador2Template')
  headerCantidadModulosGenerador2Template: TemplateRef<any>;

  @ViewChild('headerCantidadModulosGenerador3Template')
  headerCantidadModulosGenerador3Template: TemplateRef<any>;

  @ViewChild('headerTensionNominalBancoBateriasTemplate')
  headerTensionNominalBancoBateriasTemplate: TemplateRef<any>;

  @ViewChild('headerCapacidadNominalBancoBateriasTemplate')
  headerCapacidadNominalBancoBateriasTemplate: TemplateRef<any>;

  @ViewChild('headerCorrienteNominalCargadorBateriasTemplate')
  headerCorrienteNominalCargadorBateriasTemplate: TemplateRef<any>;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('viewModal')
  viewModal: ModalDirective;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  allData: SistemaFotovoltaico[];

  constructor(private alertService: AlertService, private translationService: AppTranslationService, private establecimientoService: EstablecimientoService,
    private localStorage: LocalStoreManager, private authService: AuthService, private sistemaFotovoltaicoService: SistemaFotovoltaicoService, private accountService: AccountService) {
  }

  private gT = (key: string) => this.translationService.getTranslation(key);

  ngOnInit() {
    this.loadingIndicator = true;

    this.establecimientoService.getAll().subscribe(p => {
      this.establecimientos = p;
    });

    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });
    
    this.columns = [
      {
        prop: 'establecimiento',
        name: this.gT('sistemaFotovoltaico.Establecimiento'),
        headerTemplate: this.headerEstablecimientoTemplate,
        width: 200
      },
      {
        prop: 'potenciaPicoModulo',
        name: this.gT('sistemaFotovoltaico.PotenciaPicoModulo'),
        headerTemplate: this.headerPotenciaPicoModuloTemplate,
        width: 30
      },
      {
        prop: 'tensionPicoModulo',
        name: this.gT('sistemaFotovoltaico.TensionPicoModulo'),
        headerTemplate: this.headerTensionPicoModuloTemplate,
        width: 30
      },
      {
        prop: 'potenciaPicoGenerador',
        name: this.gT('sistemaFotovoltaico.PotenciaPicoGenerador'),
        headerTemplate: this.headerPotenciaPicoGeneradorTemplate,
        width: 30
      },
      {
        prop: 'cantidadReguladoresCarga',
        name: this.gT('sistemaFotovoltaico.CantidadReguladoresCarga'),
        headerTemplate: this.headerCantidadReguladoresCargaTemplate,
        width: 30
      },
      {
        prop: 'potenciaPicoGenerador1',
        name: this.gT('sistemaFotovoltaico.PotenciaPicoGenerador1'),
        headerTemplate: this.headerPotenciaPicoGenerador1Template,
        width: 30
      },
      {
        prop: 'potenciaPicoGenerador2',
        name: this.gT('sistemaFotovoltaico.PotenciaPicoGenerador2'),
        headerTemplate: this.headerPotenciaPicoGenerador2Template,
        width: 30
      },
      {
        prop: 'potenciaPicoGenerador3',
        name: this.gT('sistemaFotovoltaico.PotenciaPicoGenerador3'),
        headerTemplate: this.headerPotenciaPicoGenerador3Template,
        width: 30
      },
      {
        prop: 'cantidadModulosGenerador1',
        name: this.gT('sistemaFotovoltaico.CantidadModulosGenerador1'),
        headerTemplate: this.headerCantidadModulosGenerador1Template,
        width: 30
      },
      {
        prop: 'cantidadModulosGenerador2',
        name: this.gT('sistemaFotovoltaico.CantidadModulosGenerador2'),
        headerTemplate: this.headerCantidadModulosGenerador2Template,
        width: 30
      },
      {
        prop: 'cantidadModulosGenerador3',
        name: this.gT('sistemaFotovoltaico.CantidadModulosGenerador3'),
        headerTemplate: this.headerCantidadModulosGenerador3Template,
        width: 30
      },
      {
        prop: 'tensionNominalBancoBaterias',
        name: this.gT('sistemaFotovoltaico.TensionNominalBancoBaterias'),
        headerTemplate: this.headerTensionNominalBancoBateriasTemplate,
        width: 30
      },
      {
        prop: 'capacidadNominalBancoBaterias',
        name: this.gT('sistemaFotovoltaico.CapacidadNominalBancoBaterias'),
        headerTemplate: this.headerCapacidadNominalBancoBateriasTemplate,
        width: 30
      },
      {
        prop: 'corrienteNominalCargadorBaterias',
        name: this.gT('sistemaFotovoltaico.CorrienteNominalCargadorBaterias'),
        headerTemplate: this.headerCorrienteNominalCargadorBateriasTemplate,
        width: 30
      },
      {
        name: '',
        width: 75,
        cellTemplate: this.actionsTemplate,
        resizeable: false,
        canAutoResize: false,
        sortable: false,
        draggable: false
      }
    ];
  }

  ngOnDestroy() {
  }
  
  fetch(cb) {
    setTimeout(() => {
      this.sistemaFotovoltaicoService.getAll().subscribe(e => {

        for (let est of e) {
          for (let p of this.establecimientos)
            if (p.id == est.establecimientoID) {
              est.establecimiento = p.nombre;
              break;
            }
        }

        cb(e);
      })
    }, 1000);
  }

  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }

  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.establecimiento));
  }

  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    this.sistemaFotovoltaicoView = row;
    this.viewModal.show();
  }

  edit(row) {
    this._isCreate = false;
    this._rowEdit = row;
    this.sistemaFotovoltaicoEdit = new SistemaFotovoltaico(row);    
    this.editorModal.show();
  }

  addElement() {
    this._isCreate = true;
    this.sistemaFotovoltaicoEdit = new SistemaFotovoltaico();
    this.editorModal.show();
  }

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate) {
      this.sistemaFotovoltaicoService.newSistemaFotovoltaico(this.sistemaFotovoltaicoEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
      this._isCreate = false;
    }
    else
      this.sistemaFotovoltaicoService.updateSistemaFotovoltaico(this.sistemaFotovoltaicoEdit).subscribe(est => this.updateSuccessHelper(), error => this.showFailedMessage(error));
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.sistemaFotovoltaicoEdit);
    this.rows.splice(0, 0, this.sistemaFotovoltaicoEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.sistemaFotovoltaicoEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage( error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }


  deleteHelper(row) {
    this.rowsCache = this.rowsCache.filter(item => item !== row);
    this.rows = this.rows.filter(item => item !== row);

    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.sistemaFotovoltaicoService.deleteSistemaFotovoltaico(row)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar') , MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }

  //get canManageSistemasFotovoltaicos() {
  //  return this.accountService.userHasPermission(Permission.manageSistemasFotovoltaicosPermission);
  //}
}
