import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { ConfiguracionEventoEndpoint } from './configuracion-evento-endpoint.service';
import { AuthService } from './auth.service';
import { ConfiguracionEvento } from '../models/configuracion-evento.model';


@Injectable()
export class ConfiguracionEventoService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private configuracionEventoEndpoint: ConfiguracionEventoEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.configuracionEventoEndpoint.getAll<ConfiguracionEvento[]>(page, pageSize);
  }

  getConfiguracionEventoByIDEndpoint(ceveId: number) {
    return this.configuracionEventoEndpoint.getConfiguracionEventoByIDEndpoint<ConfiguracionEvento>(ceveId);
  }

  updateConfiguracionEvento(ceve: ConfiguracionEvento) {
    return this.configuracionEventoEndpoint.getUpdateConfiguracionEventoEndpoint(ceve, ceve.id.toString());
  }

  newConfiguracionEvento(ceve: ConfiguracionEvento) {
    return this.configuracionEventoEndpoint.getNewConfiguracionEventoEndpoint<ConfiguracionEvento>(ceve);
  }

  deleteConfiguracionEvento(ceveId: number) {
    return this.configuracionEventoEndpoint.getDeleteConfiguracionEventoEndpoint(ceveId);
  }
}
