  ngOnDestroy() {
    clearInterval(this.interval);
  }

  refreshData(pageInfo) {
    this.fetch(pageInfo, (data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    })    
  }
  
  fetch(pageInfo, cb) {
    setTimeout(() => {

      this._Activatedroute.paramMap.subscribe(params => {
        let dataLoggerID = params.get('id');
        this.page.pageNumber = pageInfo.offset;
        if (dataLoggerID) {
          this.medicionService.getMedicionesByDataloggerId(dataLoggerID, this.page, this._dateFrom, this._dateTo).subscribe(pagedData => {
            
            this.page = pagedData.page;
            this.mediciones = pagedData.data;

            //this.mediciones = p;
            cb(pagedData.data);
          });
        }
        else {
          this.medicionService.getResults(this.page, this._dateFrom, this._dateTo).subscribe(pagedData => {

            this.page = pagedData.page;
            this.mediciones = pagedData.data;
            //this.mediciones = p;
            cb(pagedData.data);
          });
        }
      });

    }, 1000);
  }

  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }

  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.id));
  }

  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    this.medicionView = row;
    this.viewModalMedicion.show();
  }

  edit(row) {
    this._isCreate = false;
    this.medicionEdit = row;
    this.editorModal.show();
  }

  addElement() {
    this._isCreate = true;
    this.medicionEdit = new Medicion();
    this.editorModal.show();
  }

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate) {
      this.medicionService.newMedicion(this.medicionEdit).subscribe(med => this.createSuccessHelper(), error => this.showFailedMessage(error));
      this._isCreate = false;
    }
    else
      this.medicionService.updateMedicion(this.medicionEdit).subscribe(med => this.createSuccessHelper(), error => this.showFailedMessage(error));
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.medicionEdit);
    this.rows.splice(0, 0, this.medicionEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage( error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }

  deleteHelper(row) {
    this.rowsCache = this.rowsCache.filter(item => item !== row);
    this.rows = this.rows.filter(item => item !== row);

    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.medicionService.deleteMedicion(row)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar') , MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }

  FilterByDates() {
    this._dateFrom = this.dateFrom;
    this._dateTo = this.dateTo;
    this.refreshData({ offset: 0 });
  }
