import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { ConfiguracionDeRedEndpoint } from './configuracion-de-red-endpoint.service';
import { AuthService } from './auth.service';
import { ConfiguracionDeRed } from '../models/configuracion-de-red.model';
import { Config } from 'protractor';


@Injectable()
export class ConfiguracionDeRedService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private configuracionDeRedEndpoint: ConfiguracionDeRedEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.configuracionDeRedEndpoint.getAll<ConfiguracionDeRed[]>(page, pageSize);
  }

  getConfiguracionByIdEndpoint(configId: number) {
    return this.configuracionDeRedEndpoint.getConfiguracionByIdEndPoint<ConfiguracionDeRed>(configId);
  }

  updateConfiguracionDeRed(config: ConfiguracionDeRed) {
      return this.configuracionDeRedEndpoint.getUpdateConfiguracionDeRedEndPoint(config, config.id);
  }

  newConfiguracionDeRed(config: ConfiguracionDeRed) {
    return this.configuracionDeRedEndpoint.getNewConfiguracionDeRedEndPoint<ConfiguracionDeRed>(config);
  }


  deleteConfiguracionDeRed(configId: number) {
    return this.configuracionDeRedEndpoint.getDeleteConfiguracionDeRedEndPoint(configId);
  }
}
