import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { AlarmaEndpoint } from './alarma-endpoint.service';
import { AuthService } from './auth.service';
import { Alarma } from '../models/alarma.model';


@Injectable()
export class AlarmaService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private alarmaEndpoint: AlarmaEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.alarmaEndpoint.getAll<Alarma[]>(page, pageSize);
  }

  getAlarmasByDataloggerId(dataloggerId: string) {
    return this.alarmaEndpoint.getAlarmasByDataloggerId<Alarma[]>(dataloggerId);
  }

  updateAlarma(al: Alarma) {
      return this.alarmaEndpoint.getUpdateAlarmaEndpoint(al, al.id.toString());
  }

  newAlarma(al: Alarma) {
    return this.alarmaEndpoint.getNewAlarmaEndpoint<Alarma>(al);
  }


  deleteAlarma(id: number): Observable<Alarma> {
    return this.alarmaEndpoint.getDeleteAlarmaEndpoint<Alarma>(id);
  }
}
