    AppTitleService,
    AppTranslationService,
    NotificationService,
    NotificationEndpoint,
    AccountService,
    AccountEndpoint,
    LocalStoreManager,
    EndpointFactory,
    EstablecimientoEndpoint,
    EstablecimientoService,
    ConfiguracionDeRedEndpoint,
