    DataLoggerService,
    ConfiguracionMedicionesEndpoint,
    ConfiguracionMedicionesService,
    ConfiguracionEventoEndpoint,
    ConfiguracionEventoService,
    ConfiguracionDeAlarmasEndpoint,
