import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { FirmwareEndpoint } from './firmware-endpoint.service';
import { AuthService } from './auth.service';
import { Firmware } from '../models/firmware.model';


@Injectable()
export class FirmwareService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private firmwareEndpoint: FirmwareEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.firmwareEndpoint.getAll<Firmware[]>(page, pageSize);
  }

  updateFirmware(fw: Firmware) {
    if (fw.id) {
      return this.firmwareEndpoint.getUpdateFirmwareEndpoint(fw, fw.id.toString());
    }
    //else {
    //  return this.establecimientoEndpoint.getEstablecimientoByNombreEndpoint<Establecimiento>(est.nombre).pipe<Establecimiento>(
    //    mergeMap(foundEst => {
    //      est.id = foundEst.id;
    //      return this.establecimientoEndpoint.getUpdateEstablecimientoEndpoint(est, est.id.toString());
    //    }));
    //}
  }

  newFirmware(fw: Firmware) {
    return this.firmwareEndpoint.getNewFirmwareEndpoint<Firmware>(fw);
  }


  deleteFirmware(fwOrFwId: string | Firmware): Observable<Firmware> {
    if (typeof fwOrFwId === 'string' || fwOrFwId instanceof String) {
      return this.firmwareEndpoint.getDeleteFirmwareEndpoint<Firmware>(<string>fwOrFwId);
    }
    else {

      if (fwOrFwId.id) {
        return this.deleteFirmware(fwOrFwId.id.toString());
      }
      //else {
      //  return this.eventoEndpoint.getEventoByNombreEndpoint<Evento>(eveOrEveId.id).pipe<Evento>(
      //    mergeMap(eve => this.deleteEvento(eve.id.toString())));
      
    }
  }
}
