import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { ConfiguracionMedicionesEndpoint } from './configuracion-mediciones-endpoint.service';
import { AuthService } from './auth.service';
import { ConfiguracionMediciones } from '../models/configuracion-mediciones.model';


@Injectable()
export class ConfiguracionMedicionesService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private configuracionMedicionesEndpoint: ConfiguracionMedicionesEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.configuracionMedicionesEndpoint.getAll<ConfiguracionMediciones[]>(page, pageSize);
  }

  getConfiguracionMedicionesByID(cmId: number) {
    return this.configuracionMedicionesEndpoint.getConfiguracionMedicionesByIDEndpoint<ConfiguracionMediciones>(cmId);
  }

  updateConfiguracionMediciones(cm: ConfiguracionMediciones) {
    return this.configuracionMedicionesEndpoint.getUpdateConfiguracionMedicionesEndpoint(cm, cm.id.toString());
  }

  newConfiguracionMediciones(cm: ConfiguracionMediciones) {
    return this.configuracionMedicionesEndpoint.getNewConfiguracionMedicionesEndpoint<ConfiguracionMediciones>(cm);
  }

  deleteConfiguracionMediciones(cmId: number) {
    return this.configuracionMedicionesEndpoint.getDeleteConfiguracionMedicionesEndpoint(cmId);
  }
}
