import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class OrdenEndpoint extends EndpointFactory {

  private readonly _newOrdenUrl: string = '/api/orden/new';
  private readonly _getOrdenUrl: string = '/api/orden/get';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getNewOrdenEndpoint<T>(dataloggerID: string, tipoID: number): Observable<T> {
    const url = `${this._newOrdenUrl}/${dataloggerID}/${tipoID}`;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewOrdenEndpoint(dataloggerID, tipoID));
      }));
  }

  getByDataloggerID<T>(dataloggerID: string): Observable<T> {
    const url = `${this._getOrdenUrl}/${dataloggerID}`;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getByDataloggerID(dataloggerID));
      }));
  }
}
