import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { ConfiguracionDeAlarmasEndpoint } from './configuracion-de-alarmas-endpoint.service';
import { AuthService } from './auth.service';
import { ConfiguracionDeAlarmas } from '../models/configuracion-de-alarmas.model';
import { Config } from 'protractor';


@Injectable()
export class ConfiguracionDeAlarmasService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private configuracionDeAlarmasEndpoint: ConfiguracionDeAlarmasEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.configuracionDeAlarmasEndpoint.getAll<ConfiguracionDeAlarmas[]>(page, pageSize);
  }

  getConfiguracionByIdEndpoint(configId: number) {
    return this.configuracionDeAlarmasEndpoint.getConfiguracionByIdEndPoint<ConfiguracionDeAlarmas>(configId);
  }

  updateConfiguracionDeAlarmas(config: ConfiguracionDeAlarmas) {
      return this.configuracionDeAlarmasEndpoint.getUpdateConfiguracionDeAlarmasEndPoint(config, config.id);
  }

  newConfiguracionDeAlarmas(config: ConfiguracionDeAlarmas) {
    return this.configuracionDeAlarmasEndpoint.getNewConfiguracionDeAlarmasEndPoint<ConfiguracionDeAlarmas>(config);
  }


  deleteConfiguracionDeAlarmas(configId: number) {
    return this.configuracionDeAlarmasEndpoint.getDeleteConfiguracionDeAlarmasEndPoint(configId);
  }
}
