import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class FirmwareEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/firmware/all';
  private readonly _firmwareUrl: string = '/api/firmware/firmware';
  //private readonly _eventoByNombreUrl: string = '/api/establecimiento/establecimiento/nombre';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  /*getEstablecimientoByNombreEndpoint<T>(nombre: string): Observable<T> {
    const endpointUrl = `${this._eventoByNombreUrl}/${nombre}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getEstablecimientoByNombreEndpoint(nombre));
      }));
  }
  */

  getNewFirmwareEndpoint<T>(fwObject: any): Observable<T> {

    return this.http.post<T>(this._firmwareUrl, JSON.stringify(fwObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewFirmwareEndpoint(fwObject));
      }));
  }

  getUpdateFirmwareEndpoint<T>(fwObject: any, fwId: string): Observable<T> {
    const endpointUrl = `${this._firmwareUrl}/${fwId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(fwObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateFirmwareEndpoint(fwObject, fwId));
      }));
  }

  getDeleteFirmwareEndpoint<T>(fwId: string): Observable<T> {
    const endpointUrl = `${this._firmwareUrl}/${fwId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteFirmwareEndpoint(fwId));
      }));
  }
}
