export class ConfiguracionMediciones {
  public id: number;
  public dataloggerID: string;
  public datalogger: string;
  public periodoMedicion: string;
  public tensionBdB: string;
  public tensionMaximaBdBMuyBajo: number;
  public tensionMaximaBdBBajo: number;
  public tensionMaximaBdBMedio: number;
  public tensionMaximaBdBAlto: number;
  public coeficienteCorreccionPorTemperaturaTensionBdB: number;
  public comentarios: string;

  constructor(config?: ConfiguracionMediciones) {
    if (config != null) {
      this.id = config.id;
      this.dataloggerID = config.dataloggerID;
      this.datalogger = config.datalogger;
      this.periodoMedicion = config.periodoMedicion;
      this.tensionBdB = config.tensionBdB;
      this.tensionMaximaBdBMuyBajo = config.tensionMaximaBdBMuyBajo;
      this.tensionMaximaBdBBajo = config.tensionMaximaBdBBajo;
      this.tensionMaximaBdBMedio = config.tensionMaximaBdBMedio;
      this.tensionMaximaBdBAlto = config.tensionMaximaBdBAlto;
      this.coeficienteCorreccionPorTemperaturaTensionBdB = config.coeficienteCorreccionPorTemperaturaTensionBdB;
      this.comentarios = config.comentarios;
    }
  }

  public Copy(config: ConfiguracionMediciones) {
    config.id = this.id;
    config.dataloggerID = this.dataloggerID;
    config.datalogger = this.datalogger;
    config.periodoMedicion = this.periodoMedicion;
    config.tensionBdB = this.tensionBdB;
    config.tensionMaximaBdBMuyBajo = this.tensionMaximaBdBMuyBajo;
    config.tensionMaximaBdBBajo = this.tensionMaximaBdBBajo;
    config.tensionMaximaBdBMedio = this.tensionMaximaBdBMedio;
    config.tensionMaximaBdBAlto = this.tensionMaximaBdBAlto;
    config.coeficienteCorreccionPorTemperaturaTensionBdB = this.coeficienteCorreccionPorTemperaturaTensionBdB;
    config.comentarios = this.comentarios;
  }
}
